export function getCookie(name) {
    let cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      const cookies = document.cookie.split(";");
      for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
        }
      }
    }
    return cookieValue;
}

export function getFieldTypeTitle(field_type) {
  if (field_type == 'youtube_exposition')
    return "Vídeo de YouTube"
  if (field_type == 'text_exposition')
    return "Texto e Imagens"
  if (field_type == 'date')
    return "Data"
  if (field_type == 'datetime')
    return "Data e hora"
  if (field_type == 'longtext')
    return "Texto Longo"
  if (field_type == 'shorttext')
    return "Texto Curto"
  if (field_type == 'multiplechoice')
    return "Escolha Múltipla"
  if (field_type == 'selectboxes')
    return "Caixas de Seleção"
  if (field_type == 'number')
    return "Número"
  if (field_type == 'nif')
    return "NIF"
  if (field_type == 'phone')
    return "Número telefónico"
  if (field_type == 'email')
    return "Email"
  return ""
}

export function timeSince(date) {
  if (typeof date !== 'object') {
    date = new Date(date);
  }

  var seconds = Math.floor((new Date() - date) / 1000);
  var intervalType;

  var interval = Math.floor(seconds / 31536000);
  if (interval >= 1) {
    intervalType = 'ano';
  } else {
    interval = Math.floor(seconds / 2592000);
    if (interval >= 1) {
      intervalType = 'mês';
    } else {
      interval = Math.floor(seconds / 86400);
      if (interval >= 1) {
        intervalType = 'dia';
      } else {
        interval = Math.floor(seconds / 3600);
        if (interval >= 1) {
          intervalType = "hora";
        } else {
          interval = Math.floor(seconds / 60);
          if (interval >= 1) {
            intervalType = "minuto";
          } else {
            interval = seconds;
            intervalType = "segundo";
          }
        }
      }
    }
  }
  if (interval > 1 || interval === 0) {
    if (intervalType == "mês")
      intervalType = "meses"
    else
      intervalType += 's';
  }
  return interval + ' ' + intervalType;
}

export function removeExpositionSections(data) {
  for (var i = 0; i < data.form.sections.length; i++) {
    for (var j = 0; j < data.form.sections[i].questions.length; /* do nothing */) {
      if (data.form.sections[i].questions[j].properties.field_type.includes("_exposition"))
        data.form.sections[i].questions.splice(j, 1);
      else
        j++;
    }
  }
  return data;
}

export function stringIn(a, b) {
  return (
      a
        .toLowerCase()
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "")
        .indexOf(
          b
            .toLowerCase()
            .normalize("NFD")
            .replace(/[\u0300-\u036f]/g, "")
        ) != -1
    );
}

export function decodeSelectboxes(answer, api_data, sectionIndex, answerIndex) {
  for (var optionIndex = 0; optionIndex < answer.answer.length; optionIndex++) {
    const option = answer.answer[optionIndex];
    if (option === parseInt(option, 10))
      answer.answer[optionIndex] = api_data.form.sections[sectionIndex].questions[answerIndex].properties.options.filter(obj => {return obj.id === option})[0].question;
    else
      answer.answer[optionIndex] = "Outro: \"" + answer.answer + "\""
  }
}

export function decodeMultiplechoice(answer, api_data, sectionIndex, answerIndex) {
  if (answer.answer === parseInt(answer.answer, 10))
    answer.answer = api_data.form.sections[sectionIndex].questions[answerIndex].properties.options.filter(obj => {return obj.id === answer.answer})[0].question;
  else
    answer.answer = "Outro: \"" + answer.answer + "\""
}

export function decodeSections(form_responses, api_data) {
  for (var replyIndex = 0; replyIndex < form_responses.length; replyIndex++) {
    const reply = form_responses[replyIndex];
    for (var sectionIndex = 0; sectionIndex < reply.content.sections.length; sectionIndex++) {
      const section = reply.content.sections[sectionIndex];
      for (var answerIndex = 0; answerIndex < section.length; answerIndex++) {
        const answer = section[answerIndex];
        if (answer.answer !== undefined) {
          if (answer.field_type=='selectboxes')
            decodeSelectboxes(answer, api_data, sectionIndex, answerIndex);
          else if (answer.field_type=='multiplechoice')
            decodeMultiplechoice(answer, api_data, sectionIndex, answerIndex);
          else if (answer.field_type=='date')
            answer.answer = new Date(answer.answer).toLocaleDateString();
          else if (answer.field_type=='datetime')
            answer.answer = new Date(answer.answer).toLocaleString();
        }
      }
    }
  }
  return form_responses;
}